@import url("https://fonts.googleapis.com/css2?family=Chango&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap');

html,
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  
}

html {
  background-color: #f4f6fb;
}
.custom-input-control.control.has-icons-left span.icon.is-left {
  top: 2px !important;

  color: #aaa;
}
.custom-input-control.control.has-icons-left {
  display: flex;
}

.dropdown-content .custom-input-control.control.has-icons-left {
  margin-right: 3px;
}
.as-link {
  cursor: pointer;
}
.as-link:hover {
  background-color: #fafafa;
  color: #3273dc;
}
.container {
  min-width: 100%;
}
.pointer {
  cursor: pointer;
}
.white {
  color: white;
}

.flex {
  display: flex;
}
.italic {
  font-style: italic;
}
.text_center {
  text-align: center !important;
}
.text_right {
  text-align: right !important;
}
.space-right {
  margin-right: 1em !important;
}
.space-left {
  margin-left: 1em !important;
}
.space-top {
  margin-top: 1em !important;
}
.more-space-top {
  margin-top: 2em !important;
}
.space-bottom {
  margin-bottom: 1em !important;
}
.more-space-bottom {
  margin-bottom: 3em !important;
}
.animate-opacity {
  opacity: 1;  /* cant animate border without a border... */

  -webkit-animation: OpacityAnimate 1.5s ease infinite;
  -moz-animation: OpacityAnimate 1.5s ease infinite;
  -o-animation: OpacityAnimate 1.5s ease infinite;
  animation: OpacityAnimate 1.5s ease infinite;
}
@keyframes OpacityAnimate {
  0%   { opacity:1; }
  50% { opacity:0.5}
  100%   { opacity:1 }
}